.bookshelf-container
{
    display: flex;
    justify-content: space-between;
    text-align: center;
}
.bookshelf-nav
{
    margin: 0 1%;
    width: 10%;
    min-width: 200px;
    border-radius: 10px;
    user-select: none;
    min-height: 73vh;
}
.bookshelf-nav .title
{
    font-size: 25px;
    margin: 10px auto 25px auto;
}
.bookshelf-nav ul
{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.bookshelf-nav ul li
{
    width: 100%;
    height: auto;
    margin: 10px 0;
}
.bookshelf-nav ul li button
{
    background: transparent;
    width: 100%;
    height: 100%;
    border: none;
    cursor: pointer;
    padding: 5px 0;
    border-radius: 12px;
}
.bookshelf-nav ul li button p, .resp-bookshelf-top ul li button p
{
    font-size: 18px;
    margin: 2.5px auto;
}
.bookshelf-nav ul li button img
{
    width: 50px;
    height: auto;
}
.bookshelf-mid
{
    width: 80%;
    margin: auto;
    border-radius: 10px;
    min-height: 73vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.bookshelf-mid .title
{
    margin: 15px auto 50px auto;
    font-size: 35px;
    font-weight: normal;
    user-select: none;
}
.bookshelf-mid .subtitle
{
    margin: 10px auto;
    font-size: 25px;
    font-weight: normal;
    user-select: none;
}
.bookshelf-mid .text
{
    width: 90%;
    max-width: 90%;
    text-align: center;
    font-size: 18px;
    font-weight: normal;
    margin: 15px auto 25px auto;
}
.bookshelf-mid .text .link
{
    color: #5000ca;
}
.bookshelf-mid .book-search-container, .bookshelf-mid .featured-container, .bookshelf-mid .suggestion-container
{
    width: 100%;
    padding: 10px 0;
    text-align: center;
}
.bookshelf-mid .book-search-container .book-search
{
    padding: 7.5px 10px;
    margin: 10px auto;
    width: 35%;
    min-width: 300px;
    border: none;
    border-radius: 20px;
    font-size: 20px;
    background: rgb(220, 220, 220);
}
.bookshelf-mid .book-search-container .results
{
    width: 80%;
    min-width: 300px;
    height: fit-content;
    min-height: 250px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 10px auto 0 auto;
}
.featured-card-container::-webkit-scrollbar
{
    height: 10px;
}
.featured-card-container::-webkit-scrollbar-corner
{
    display: none;
    visibility: hidden;
}
.bookshelf-mid .book-search-container .results .nocontent-fill
{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    flex-direction: column;
}
.bookshelf-mid .book-search-container .results .nocontent-fill .nocontent-dots
{
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    flex-direction: row;
    height: 60px;
}
.bookshelf-mid .book-search-container .results .nocontent-fill .nocontent-dot
{
    width: 20px;
    height: 20px;
    background: rgba(128, 128, 128, 0.603);
    border-radius: 50%;
    margin: auto 10px;
}
.bookshelf-mid .book-search-container .results .nocontent-fill .nocontent-txt
{
    font-size: 22px;
    color: gray;
}

.bookshelf-mid .featured-container .featured-card-container
{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    overflow: scroll;
    width: 90%;
    min-width: 300px;
    margin: auto;
    height: 250px;
}