.login-container .login-mid
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    width: 95%;
    margin: auto;
    min-height: 73vh;
    height: fit-content;
    user-select: none;
    border-radius: 10px;
}
.login-container .login-mid .illustration
{
    width: 40%;
    height: auto;
    -webkit-user-drag: none;
    margin-right: 10%;
}
.error-box
{
    border: thin solid transparent;
    border-radius: 12px;
    padding: 5px 0;
    margin: 5px auto 15px auto;
    width: 95%;
    text-align: center;
    font-weight: normal;
    font-size: 17px;
}
.login-container .login-mid .form-holder
{
    height: fit-content;
    min-height: 60vh;
    width: 40%;
    min-width: 300px;
    border-radius: 12px;
    box-shadow: 0 0 5px 0 black;
}
.form-holder .form-title
{
    font-size: 30px;
    text-align: center;
    padding-top: 15px;
    color: white;
    width: 100%;
    background: #5000ca;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}
.form-holder svg
{
    height: 12.5%;
    width: 100%;
}
.form
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    width: 100%;
    margin-top: 10px;
    font-family: Arial, Helvetica, sans-serif;
}
.input-holder
{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 70%;
    margin: 20px auto;
    font-size: 20px;
    cursor: text;
}
.form .email, .form .password
{
    background: transparent;
    padding: 7.5px 12.5px;
    font-size: 20px;
    border-radius: 20px;
    border: 3px solid;
    width: 100%;
    transition: all 0.2s;
    margin: auto;
    z-index: 1;
}
.form .email:focus, .form .password:focus
{
    border-color: #5000ca;
}
.form .email::placeholder, .form .password::placeholder
{
    display: none;
    visibility: hidden;
    color: transparent;
}
.form .input-holder .placeholder
{
    position: absolute;
    padding: 0 12.5px;
    border: 5px solid transparent;
    transition: all 0.25s;
    cursor: default;
    user-select: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: gray;
}
.form .input-holder .password
{
    border-right: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}
.form .input-holder .password-vis
{
    position: relative;
    padding: 9.5px 0;
    border: 3px solid;
    border-radius: 20px;
    border-left: none;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    transition: all 0.2s;
    cursor: pointer;
    user-select: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    background: transparent;
    z-index: 1;
}
.form .input-holder .placeholder img, .form .input-holder .password-vis img
{
    height: 20px;
    width: auto;
    margin: auto 5px;
}
.form .button
{
    color: white;
    background: #5000ca;
    border-radius: 20px;
    padding: 10px 15px;
    font-size: 20px;
    cursor: pointer;
    margin-top: 12.5%;
    border: none;
    width: 50%;
    transition: all 0.2s;
}
.form .button:hover
{
    background: #6915e7;
}