.book
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    border-radius: 10px;
    margin: 5px 15px;
    height: 200px;
    user-select: none;
    padding: 10px;
    width: 175px;
}
.book .book-img
{
    height: 100px;
    width: auto;
}
.book .label
{
    color: gray;
    font-size: 15px;
    text-align: left;
    align-self: flex-start;
}
.book .book-name, .book .book-author, .book .book-recby
{
    font-size: 16px;
    text-align: left;
    align-self: flex-start;
}