.book-mid
{
    width: 95%;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
}
.book-mid .subtitle
{
    font-size: 25px;
    text-align: center;
    font-weight: bold;
    margin: 15px auto 10px auto;
}
.book-mid .book-image
{
    width: 50%;
    height: auto;
    margin: 20px auto;
}
.book-mid .book-title
{
    font-size: 35px;
    font-weight: bold;
    text-align: center;
    margin: 20px auto;
}
.book-mid .basic-info
{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    font-family: Arial;
    font-size: 18px;
    padding-bottom: 15px;
    border-bottom: 2px solid;
    width: 80%;
    margin: 10px auto;
}
.book-mid .basic-info .info
{
    font-size: 18px;
    margin: 2.5px 5px;
}
.book-mid .basic-info .info .label
{
    color: rgb(134, 134, 134);
}
.book-mid .book-info
{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.book-mid .book-status
{
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.book-mid .book-status .status
{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 10px auto;
    width: 100%;
    font-size: 21px;
    text-align: center;
}
.book-mid .book-status .status .book-aval-i, .book-mid .book-status .status .book-quantity-i
{
    height: 25px;
    width: auto;
    margin-right: 10px;
}
.book-resume
{
    width: 100%;
}
.book-resume .text
{
    width: 90%;
    text-align: justify;
    margin: auto;
}