.home-container
{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: center;
}

.home-mid
{
    width: 95%;
    border-radius: 10px;
    margin: auto;
}

.home-left, .home-right
{
    width: 15%;
    border-radius: 10px;
}

.home-left, .home-right
{
    margin: 0 1%;
}

.home-mid .title
{
    margin: 15px auto;
    font-size: 35px;
    font-family: Raleway;
    font-weight: normal;
    user-select: none;
}
.home-illustration
{
    width: 70%;
    height: auto;
    margin: auto;
    user-select: none;
    -webkit-user-drag: none;
}
.home-mid .text
{
    width: 90%;
    max-width: 90%;
    text-align: justify;
    text-indent: 20px;
    font-size: 18px;
    font-family: Raleway;
    font-weight: normal;
    margin: 15px auto;
}
.home-mid .text .link
{
    color: #5000ca;
}

.home-right .title, .home-left .title
{
    width: fit-content;
    margin: 15px auto;
    font-size: 20px;
    font-family: Raleway;
    font-weight: normal;
    user-select: none;
    padding-bottom: 10px;
    border-bottom: thin solid;
}

.home-right .link-list
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.home-right .link-list li
{
    margin: 7.5px auto;
}
.home-right .link
{
    font-size: 18px;
    font-family: Raleway;
    font-weight: normal;
    user-select: none;
}