.profile-mid
{
    width: 95%;
    margin: auto;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profile-mid .user-info
{
    display: flex;
    flex-direction: row;
    width: 98%;
    margin: auto;
    padding: 10px 0;
}
.profile-mid .user-info .user-image
{
    width: 20%;
    height: auto;
}
.profile-mid .user-info .user-namerole
{
    margin: auto 15px;
}  
.profile-mid .user-info .user-name
{
    font-size: 30px;
    font-weight: bold;
}
.profile-mid .user-info .user-role
{
    color: gray;
    font-size: 18px;
}