@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@200;300;400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@300&display=swap');

*
{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  font-family: "Raleway", Arial;
}

::selection
{
  background: #5000ca;
}
::-webkit-scrollbar
{
  background: transparent;
  width: 10px;
}
::-webkit-scrollbar-thumb
{
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  background: #3e009b;
}
::-webkit-scrollbar-button
{
  display: none;
}

html, body, #root, .App
{
  height: fit-content;
  width: 100%;
  min-width: 315px;
}

.breadcrumb
{
  width: 95%;
  border-radius: 20px;
  margin: 20px auto 0 auto;
  font-size: 18px;
  user-select: none;
}
.breadcrumb nav
{
  margin-left: 10px;
  padding: 5px 0;
}
.breadcrumb img
{
  width: auto;
  height: 15px;
  margin: auto 5px;
}
.breadcrumb span
{
  color: gray;
}
header
{
  height: fit-content;
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  background: transparent;
}
header .logo-link
{
  color: white;
  font-weight: normal;
  font-size: 30px;
  margin-left: 15px;
}
header .logo-link img
{
  width: 30px;
  height: 30px;
  margin-right: 10px;
  height: auto;
}
header .header-nav .header-nav-list
{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.header-nav .header-nav-list li
{
  margin: 0 15px;
}
.header-nav-list .link
{
  color: white;
  font-size: 22.5px;
  transition: all 0.15s;
}
.header-nav-list .link:hover
{
  color: rgb(230, 230, 230);
}
.nav-dropdown
{
  cursor: pointer;
  margin: 0 auto;
  text-align: center;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: fit-content;
}
.nav-dropdown .nav-dropdown-links
{
  position: absolute;
  margin: auto;
  background: #2a0068;
  padding: 2.5px 20px;
  display: none;
  flex-direction: column;
  align-items: center;
  border-radius: 12px;
  cursor: default;
  justify-content: center;
  margin-top: 190px;
}
.resp-nav .nav-dropdown
{
  flex-direction: column;
}
.resp-nav .nav-dropdown-links
{
  position: relative;
  margin-top: 0;
}
.nav-dropdown .nav-dropdown-links li
{
  margin: 5px auto;
  width: 100%;
}
.nav-dropdown-links li .link
{
  width: 100%;
}
.nav-dropdown .nav-dropdown-title
{
  color: white;
  font-size: 22.5px;
  transition: all 0.15s;
  margin: auto;
  width: 100%;
  height: 100%;
}
.nav-dropdown .nav-dropdown-title:hover
{
  color: rgb(230, 230, 230);
}
.header-nav-list .nav-dropdown img
{
  width: 15px;
  height: 15px;
  margin-left: 5px;
}
header .top-header
{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 11vh;
  user-select: none;
  background: #5000ca;
  z-index: 1;
}
header .top-header .header-userarea
{
  margin-right: 15px;
}
header .top-header .header-userarea-nosession, .resp-nav .header-userarea-nosession
{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
header .header-userarea-session
{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: default;
  border-radius: 20px;
}
header .header-userarea-session div
{
  display: flex;
  flex-direction: row;
  border-radius: 20px;
}
header .header-userarea-session .top-userarea
{
  transition: all 0.2s;
  padding: 7.5px 0px 7.5px 10px;
}
header .header-userarea-session .top-userarea:hover
{
  background: rgba(255,255,255,0.2);
}
header .header-userarea-session .user-options
{
  position: absolute;
  margin-top: 160px;
  background: #2a0068;
  width: 200px;
}
header .header-userarea-session .user-options ul
{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 5px 10px;
}
header .header-userarea-session .user-options ul li
{
  margin: 5px auto;
}
header .header-userarea-session .user-options ul li .link
{
  color: white;
  background: transparent;
  border: none;
  font-size: 20px;
  cursor: pointer;
}
header .resp-nav .header-userarea-session .user-options
{
  position: relative;
  margin-top: 0;
  width: 100%;
}
header .resp-nav .header-userarea-session .user-options, header .header-userarea-session .user-options
{
  display: none;
}
header .header-userarea-session .greetings
{
  font-size: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  color: white;
}
header .header-userarea-session .greetings img
{
  width: 20px;
  height: 20px;
  margin: auto 2.5px;
}
header .top-header .header-userarea-nosession .link, .resp-nav .header-userarea-nosession .link
{
  color: white;
  font-size: 19px;
  font-weight: bold;
  padding: 10px 12.5px;
  background-color: cornflowerblue;
  border-radius: 20px;
  transition: all 0.15s;
}
header .top-header .header-userarea-nosession .link:hover, .resp-nav .header-userarea-nosession .link:hover
{
  background: rgb(118, 166, 255);
}
header .top-header .header-userarea-nosession span, .resp-nav .header-userarea-nosession span
{
  color: white;
  font-size: 18px;
  font-weight: normal;
  margin: 0 15px;
}
header .theme-button img, header .logout-button img
{
  height: 30px;
  width: 30px;
  transition: all 0.25s;
  margin: auto;
  filter: invert(100%);
}
header .theme-button, header .logout-button
{
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  width: fit-content;
  height: fit-content;
  background: transparent;
  margin: auto 15px;
}
header .header-userarea-session .theme-button, header .header-userarea-session .logout-button
{
  margin: auto 5px auto 15px;
}
header .header-userarea-session .user-image
{
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: auto 10px auto 0;
}
header svg
{
  width: 100%;
  height: 25vh;
}
.resp-nav-button
{
  display: none;
  width: 50px;
  height: 50px;
  cursor: pointer;
  border-radius: 50%;
  border: 3px solid white;
  background: transparent;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
}
.resp-nav-button::after
{
  background: chocolate;
  width: 100%;
  height: 100%;
  z-index: 2;
}
.resp-nav-button .resp-nav-button-line
{
  display: block;
  width: 30px;
  margin: 3px auto;
  border: 2px solid white;
  border-radius: 10px;
  background: white;
}
.resp-nav
{
  display: none;
  width: 100%;
  height: fit-content;
  background: #3e009b;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  user-select: none;
  position: relative;
}
.resp-nav .title
{
  color: white;
  margin: 20px auto 10px auto;
  font-size: 30px;
}
.resp-nav .header-userarea
{
  margin: 15px auto 5px auto;
}
.resp-nav .header-nav-list
{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  text-align: center;
  width: 100%;
}
.resp-nav .header-nav-list .link, .resp-nav .header-nav-list .nav-dropdown .nav-dropdown-title
{
  font-size: 25px;
}
.resp-nav .header-nav-list li
{
  width: 100%;
  margin: 7.5px auto;
  text-align: center;
}
.resp-nav .close-bottom
{
  width: 100%;
  background: #5000ca;
  height: 20px;
  cursor: pointer;
  border: none;
}
.resp-nav .close-bottom .close-line
{
  width: 40%;
  min-width: 100px;
  height: 6px;
  margin: auto;
  border-radius: 10px;
  background: white;
  display: block;
  transition: all 0.2s;
}
.content
{
  width: 100%;
  margin: 3vh 0;
  height: fit-content;
  min-height: 64vh;
}

.backtop-btn
{
  position: fixed;
  bottom: 50px;
  left: 20px;
  z-index: 1;
  background: transparent;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: none;
  user-select: none;
}
.backtop-btn img
{
  width: auto;
  height: 50px;
}

footer
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #5000ca;
  width: 100%;
  height: fit-content;
  position: relative;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 5px 0 black;
}








button, ul, li, input, a
{
  outline: none;
  list-style: none;
  text-decoration: none;
}

@media screen and (min-width: 0px) and (max-width: 1120px)
{
  .bookshelf-container
  {
    flex-direction: column;
  }
  .bookshelf-container .bookshelf-nav
  {
    width: 95%;
    height: fit-content;
    min-height: unset;
    margin: 10px auto;
  }
  .bookshelf-container .bookshelf-nav .title
  {
    display: block;
  }
  .bookshelf-container .bookshelf-nav ul
  {
    flex-direction: row;
    flex-wrap: nowrap;
    margin: 10px auto;
  }
  .bookshelf-container .bookshelf-nav ul li
  {
    width: 200px;
    height: 100%;
    margin: 5px auto;
  }
  .bookshelf-container .bookshelf-nav ul li button
  {
    width: 100%;
    height: 100%;
    padding: 5px 0;
  }
  .bookshelf-container .bookshelf-nav ul li button img
  {
    width: 50px;
    height: auto;
  }
  .bookshelf-container .bookshelf-mid
  {
    width: 95%;
  }
}
@media screen and (min-width: 0px) and (max-width: 910px)
{
  header svg
  {
    display: none;
  }
  .header-nav
  {
    display: none;
  }
  .top-header .header-userarea
  {
    display: none;
  }
  .resp-nav-button
  {
    display: flex;
  }
  .home-left, .home-right
  {
    display: none;
  }
  .home-mid
  {
    width: 95%;
    margin: auto;
  }
  .resp-nav .header-userarea-nosession .link
  {
    font-size: 17px;
  }
  .login-mid .illustration, .register-mid .illustration
  {
    display: none;
  }
  .login-container .login-mid .form-holder, .register-container .register-mid .form-holder
  {
    margin-right: auto;
    margin: auto;
  }
}
@media screen and (min-width: 0px) and (max-width: 700px)
{
  .bookshelf-container .bookshelf-nav ul li
  {
    width: 110px;
  }
  .bookshelf-container .bookshelf-nav ul li button img
  {
    width: 35px;
    height: auto;
  }
  .bookshelf-container .bookshelf-nav ul li button p
  {
    font-size: 14px;
  }
  .book-container .book-mid .basic-info .info
  {
    font-size: 16px;
    margin: 2.5px auto;
  }
  .book-container .book-mid .book-title
  {
    font-size: 30px;
  }
}
@media screen and (min-width: 0px) and (max-width: 400px)
{
  .book-container .book-mid .book-title
  {
    font-size: 25px;
  }
  .book-container .book-mid .basic-info .info
  {
    font-size: 15px;
  }
  .book-container .book-mid .book-status .status
  {
    font-size: 16px;
  }
  .bookshelf-container .book-mid .book-aval-i
  {
    height: 15px;
  }
}

.cr-txt
{
  color: gray;
  font-size: 15px;
  user-select: none;
  margin-top: auto;
  margin-bottom: 10px;
  text-align: center;
  max-width: 95%;
}
.footer-devby
{
  background: black;
  width: 100%;
  padding: 2.5px 0;
  user-select: none;
}
.devby-text
{
  color: gray;
  font-size: 16px;
  font-family: "Montserrat Alternates", Arial;
  text-align: center;
}
.kosmicgradient
{
  background: -webkit-linear-gradient(to , rgb(94, 0, 94), rgb(161, 0, 161));
  background: -o-linear-gradient(to right, rgb(94, 0, 94), rgb(161, 0, 161));
  background: -moz-linear-gradient(to right, rgb(94, 0, 94), rgb(161, 0, 161));
  background: linear-gradient(to right, rgb(94, 0, 94), rgb(161, 0, 161));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 16px;
  font-family: "Montserrat Alternates", Arial;
  font-weight: bold;
  cursor: pointer;
}

.notfound-container
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  user-select: none;
  text-align: center;
}
.notfound-container img
{
  width: 80%;
  height: auto;
}
.notfound-container h3
{
  font-size: 25px;
  font-weight: normal;
  letter-spacing: 5px;
}
.notfound-container a
{
  font-size: 20px;
  color: #5000ca;
}